<template>
  <div class="form">
    <div v-if="form.id" class="form__item">
      <div class="form__label">
        <label for="id">id</label>
      </div>
      <input :value="form.id" disabled class="form__input" id="id" placeholder="id" type="text">
    </div>
    <div class="form__item">
      <div class="form__label">
        <label for="TypeOfSport">Вида спорта</label>
      </div>
      <input v-model="form.TypeOfSport" class="form__input" id="TypeOfSport" placeholder="Вид спорта" type="text">
    </div>
    <div class="form__item">
      <Btn @click="onClick" :disabled="!isValidForm" class="info">Сохранить</Btn>
    </div>
  </div>
</template>

<script>
import { computed, reactive, onBeforeMount, watchEffect } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

import { selectItemById, fetchTypeOfSportItems } from '@/store/typeofsport/selectors';
import Btn from '@/components/Btn/Btn';
export default {
  name: 'TypeOfSportForm',
  components: {
    Btn,
  },
  props: {
    id: { type: String, default: '' },
  },
  setup(props, context) {
    const store = useStore();
    const router = useRouter();
    const form = reactive({
      id: '',
      TypeOfSport: '',
    });

    onBeforeMount(() => {
      fetchTypeOfSportItems(store);
    });


    watchEffect(() => {
      const TypeOfSport = selectItemById( store,  props.id );
      Object.keys(TypeOfSport).forEach(key => {
        form[key] = TypeOfSport[key]
      })
    });

    return {
      form,
      isValidForm: computed(() =>  !!(form.TypeOfSport)),
      onClick: () => {
        console.log(form);
        context.emit('submit', form);
        router.push({ name: 'TypeOfSport' })
      },
    }

  }
}
</script>