<template>
  <Layout :title="id ? 'Редактирование записи' : 'Создание записи'">
    <TypeOfSportForm
        :id="id"
        @submit="onSubmit"
    />
  </Layout>
</template>

<script>
import { useStore } from 'vuex';

import { updateItem, addItem } from '@/store/typeofsport/selectors';
import Layout from '@/components/Layout/Layout';
import TypeOfSportForm from '@/components/TypeOfSportForm/TypeOfSportForm';
export default {
  name: 'TypeOfSportEdit',
  props: {
    id: String,
  },
  components: {
    Layout,
    TypeOfSportForm,
  },
  setup() {
    const store = useStore();
    return {
      onSubmit: ({ id, TypeOfSport }) => id ?
          updateItem(store, { id, TypeOfSport }) :
          addItem(store, { id, TypeOfSport }),
    };
  }
}
</script>

